<script>
'use strict';

import ZProbeValues from '../components/EndstopsZProbeValues.vue'

const probeTypes = ["noprobe", "switch", "unmodulated", /*"modulated",*/ /*"ultrasonic",*/ "effector", "bltouch"];

export default {
	beforeMount() {
	},
	components: {
		"z-probe-values": ZProbeValues
	},
	computed: {
		pwmChannel: {
			get() {
				this.template.probe.pwm_channel = 0; //default to first "RRF GPIO port"

				if(this.board.lpc.servoPins.length >0 ) return this.board.lpc.servoPins[0].value;
				
				this.showManualServoPin = true;
				return "Manual"
			},
			set(value) {
				if(value == "Manual"){
					this.showManualServoPin = true;
					// we limit to 3 entries in servo entries
					//if manual is an option then it will be after existing servo entries					
					this.template.probe.pwm_channel = this.board.lpc.servoPins.length;

				} else {
					this.showManualServoPin = false;
					this.template.lpc.manualServoPin = "NoPin";
					this.template.probe.pwm_channel = 0;

					//find the Index of the "RRF GPIO Port"
					for(var i=0; i<this.board.lpc.servoPins.length; i++){
						if(this.board.lpc.servoPins[i].value === value) {
							this.template.probe.pwm_channel = i;
						}
					}
				}
			}
		},
		
		probeType: {
			get() {
				const probeType = this.template.probe.type;
				return probeType;
				//return probeTypes.findIndex(item => item == probeType);
			},
			set(value) {


				const prevProbeType = this.template.probe.type;
				//this.template.probe.type = probeTypes[value];

				this.template.probe.type = value;

				if(this.template.probe.type != "bltouch") this.template.lpc.manualServoPin = "NoPin"; //reset the manual pin if moved away from bltouh tab


				//console.log(value + " = "+ this.template.probe.type);

				// Set default probe speed when switching to/from effector
				if (this.template.probe.type == "effector") {
					this.template.probe.speed = 20;
				} else if (prevProbeType == "effector") {
					this.template.probe.speed = this.preset.probe.speed;
				}
			}
		}
	},
	data() {
		return {
			pwmChannels: [],
			showManualServoPin: false,
		}
	}
}
</script>
